import React from "react";
import { Link } from "react-router-dom";
import {
  FaInstagram,
  FaFacebookF,
  FaBullseye,
  FaClipboardList
} from "react-icons/fa";

import styles from "./css/service.module.css";
import genaralStyles from "../Layout/css/general.module.css";

const service = [
  { icon: FaInstagram, text: "Продвижение Instagram" },
  { icon: FaFacebookF, text: "Продвижение Facebook" },
  { icon: FaBullseye, text: "Таргетинг" },
  {
    icon: FaClipboardList,
    text: "Создание контента",
    smallText: "+ фотосъемка"
  }
];

const Service = () => {
  return (
    <div className={genaralStyles.container}>
      <h2 className={genaralStyles.title}>Услуги, которые мы предоставляем</h2>
      <ul className={styles.serviceContainer}>
        {service.map(({ icon: Icon, text, smallText }) => (
          <li key={text} className={styles.seriveItem}>
            <div className={styles.itemContainer}>
              <Icon className={styles.icon} />{" "}
              <div className={styles.verticalLine} />
              <span className={styles.text}>{text}</span>
              {smallText && (
                <span className={styles.smallText}>{smallText}</span>
              )}
              <Link className={styles.button} to="/contacts">
                Заказать
              </Link>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Service;
