import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { FaBars, FaTimes } from "react-icons/fa";

import styles from "./css/burderMenu.module.css";

const links = [
  {
    label: "Услуги",
    href: "/service"
  },
  {
    label: "Результаты",
    href: "/results"
  },
  {
    label: "Этапы",
    href: "/steps"
  },
  {
    label: "О нас",
    href: "/about"
  },
  {
    label: "Контакты",
    href: "/contacts"
  }
];

const BurgerMenu = ({ isAuthenticated, user, logout }) => {
  const [isOpen, setIsOpen] = useState(false);

  const handleOpenClick = () => {
    setIsOpen(true);
  };

  const handleCloseClick = () => {
    setIsOpen(false);
  };

  return (
    <>
      <div onClick={handleOpenClick} className={styles.openButton}>
        <FaBars />
      </div>
      <section
        className={[styles.container, isOpen ? styles.open : null].join(" ")}
      >
        <div onClick={handleCloseClick} className={styles.closeButton}>
          <FaTimes />
        </div>

        <ul className={styles.linkList}>
          {links.map(({ label, href }) => (
            <li key={href} className={styles.linkContainer}>
              <NavLink
                activeClassName={styles.active}
                className={styles.link}
                onClick={handleCloseClick}
                to={href}
              >
                {label}
              </NavLink>
            </li>
          ))}
        </ul>
      </section>
    </>
  );
};

export default BurgerMenu;
