import React from "react";

import styles from "./css/contacts.module.css";
import genaralStyles from "../Layout/css/general.module.css";

const Contacts = () => {
  return (
    <div className={genaralStyles.container}>
      <h2 className={genaralStyles.title}>Контакт с нами</h2>
      <section className={styles.contactsContainer}>
        <div className={styles.contactItem}>
          <span className={styles.label}>Телефон</span>
          <span className={styles.value}>0-800-224-488</span>
        </div>
        <div className={styles.contactItem}>
          <span className={styles.label}>Email</span>
          <span className={styles.value}>info@gmail.com</span>
        </div>
        <div className={styles.contactItem}>
          <span className={styles.label}>Адрес</span>
          <span className={styles.value}>г. Киев, ул. Название, 42</span>
        </div>
      </section>

      <div className={styles.formContainer}>
        <h2 className={styles.formTitle}>Заказать консультацию</h2>
        <form className={styles.form}>
          <input className={styles.input} type="text" placeholder="Ваше имя" />
          <input
            className={styles.input}
            type="text"
            placeholder="Ваш телефон"
          />
          <input
            className={styles.input}
            type="text"
            placeholder="Ваш E-mail"
          />
          <button className={styles.button} type="submit">
            Заказать
          </button>
        </form>
      </div>
    </div>
  );
};

export default Contacts;
