import React from "react";

import styles from "./css/about.module.css";
import genaralStyles from "../Layout/css/general.module.css";

const About = () => {
  return (
    <div className={genaralStyles.container}>
      <h2 className={genaralStyles.title}>Уcпешные клиенты выбирают ALMA</h2>
      <ul className={styles.featuresList}>
        <li className={styles.feature}>
          <span className={styles.value}>7 лет</span>
          <span className={styles.label}>опыта</span>
        </li>
        <li className={styles.feature}>
          <span className={styles.value}>300+</span>
          <span className={styles.label}>проектов</span>
        </li>
        <li className={styles.feature}>
          <span className={styles.value}>12</span>
          <span className={styles.label}>експертов</span>
        </li>
      </ul>
      <section className={styles.slider}>
        <div className={styles.container}>
          <div className={styles.slide}>Asters</div>
          <div className={styles.slide}>Asters</div>
          <div className={styles.slide}>Asters</div>
          <div className={styles.slide}>Asters</div>
        </div>
      </section>
    </div>
  );
};

export default About;
