import React from "react";

const gradient = () => {
  return (
    <svg
      style={{ width: 0, height: 0, position: "absolute" }}
      aria-hidden="true"
      focusable="false"
    >
      <linearGradient id="my-cool-gradient" x2="1" y2="1">
        <stop offset="0%" stopColor="#e0682a" />
        <stop offset="100%" stopColor="#59213e" />
      </linearGradient>
    </svg>
  );
};

export default gradient;
