import React from "react";
import {
  FaWifi,
  FaMapMarkedAlt,
  FaGem,
  FaUserTie,
  FaShoppingCart,
  FaBullhorn,
  FaUsers,
  FaUserFriends
} from "react-icons/fa";

import styles from "./css/results.module.css";
import genaralStyles from "../Layout/css/general.module.css";
import Gradient from "./svgGradients/";

const results = [
  {
    icon: FaWifi,
    text: "Гиганский охват"
  },

  {
    icon: FaMapMarkedAlt,
    text: "Выход на целевую аудиторию"
  },

  {
    icon: FaGem,
    text: "Имидж образа бренда"
  },

  {
    icon: FaUserTie,
    text: "Регулярный контакт с аудиторией"
  },

  {
    icon: FaShoppingCart,
    text: "Большие и быстрые продажи"
  },

  {
    icon: FaBullhorn,
    text: "Реклама продукта компании"
  },

  {
    icon: FaUsers,
    text: "Диалог с целевой аудиторией"
  },

  {
    icon: FaUserFriends,
    text: "Возможность взаимной рекламы"
  }
];

const Results = () => {
  return (
    <div className={genaralStyles.container}>
      <Gradient />
      <h2 className={genaralStyles.title}>Мы решаем ваши бизнесс-цели</h2>
      <ul className={styles.resultsContainer}>
        {results.map(({ icon: Icon, text }) => (
          <li className={styles.resultsItem}>
            <Icon className={styles.icon} />
            {text}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Results;
