import React from "react";

import styles from "./css/startPage.module.css";
import genaralStyles from "../Layout/css/general.module.css";

const StartPage = () => {
  return (
    <div className={genaralStyles.container}>
      <h1 className={genaralStyles.title}>Продвижение соц сетей</h1>
      <p className={styles.subTitle}>
        Потенциальные клиенты запомнят Ваше сообщество
      </p>
      <a className={styles.cta} href="/">
        Заказать <span className={styles.arrow} />
      </a>
    </div>
  );
};

export default StartPage;
