import React from "react";
import { FaFacebookF, FaInstagram } from "react-icons/fa";
import { Link } from "react-router-dom";

import logoMob from "../assets/logo-mobile.png";
import logoTab from "../assets/logo-tablet.png";
import logoDesk from "../assets/logo-desktop.png";
import styles from "./css/sidebar.module.css";

const SideBar = () => {
  return (
    <div className={styles.container}>
      <Link to="/">
        <picture>
          <source srcSet={logoDesk} media="(min-width: 1024px)" />
          <source srcSet={logoTab} media="(min-width: 768px)" />
          <img className={styles.logo} src={logoMob} alt="Company logo" />
        </picture>
      </Link>
      <div className={styles.pageNumber}>01</div>
      <div className={[styles.pageSymbol, styles.active].join(" ")} />
      <div className={styles.pageSymbol} />
      <div className={styles.pageSymbol} />
      <div className={styles.pageSymbol} />
      <div className={styles.pageSymbol} />
      <div className={styles.pageSymbol} />
      <div
        className={styles.social}
        style={{ marginBottom: "7px", marginTop: "70px" }}
      >
        <FaFacebookF className={styles.icon} />
      </div>
      <div className={styles.social}>
        <FaInstagram className={styles.icon} />
      </div>
    </div>
  );
};

export default SideBar;
