import React from "react";

import styles from "./css/steps.module.css";
import genaralStyles from "../Layout/css/general.module.css";

const steps = [
  "Анализируем рынок и конкурентов",
  "Разрабатываем маркетинговую стратегию",
  "Составляем контент-план публикаций",
  "Проводим фотосессии",
  "Создаем уникальный дизайн",
  "Пишем эффективные и прожающие тексты",
  "Публикуем посты и истории в соц сетях"
];

const Steps = () => {
  return (
    <div className={genaralStyles.container}>
      <h2 className={genaralStyles.title}>Как мы работаем</h2>
      <ul className={styles.stepsContainer}>
        {steps.map(step => (
          <li className={styles.step}>{step}</li>
        ))}
      </ul>
    </div>
  );
};

export default Steps;
